@value colorFirst,colorSecond from "../../common/colors.css";
.container {
  width: 100%;
  min-height: 100vh;
  background-color: colorFirst;
  z-index: -1;
}
.bg_subcolor {
  width: 100%;
  height: 30vh;
  position: absolute;
  bottom: 0;
  background-color: colorSecond;
  z-index: 5;
}
.content {
  position: absolute;
  right: 0px;
  width: 73%;
  min-height: 100vh;
  z-index: 10;
}

@media screen and (max-width: 1200px) {
  .content {
    width: 100%;
  }
}
